@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lora";
    font-weight: 500;
}

html {
    background: linear-gradient(180deg, #2D3A4E, #5D7083) fixed;
}

#background {
    margin: 0;
    padding: 0;
    background: linear-gradient(180deg, #2D3A4E, #5D7083) fixed;
}
  
#main-container {
    max-width: 792px;
    min-height: calc(100vh - 50px);
}

#nav-container {
    margin-left: 0.5rem;
}

#nav-title {
    padding: 3rem 0;
}

#nav-title h1 {
    font-size: 30px;
    margin: auto 0;
    color: white;
}

#nav-buttons {
    padding: 3rem 0;
}

.nav-button {
    margin-left: 0.5rem;
}

#lists-container {
    margin-bottom: 8rem;
}

#footer-container {
    background-color: black;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 50px;
}

#footer-container p {
    margin: 0.75rem 0;
    color: white;
}

@media (max-width: 575px) {
    #main-container {
        min-height: calc(100vh - 45px);
    }

    #nav-container {
        flex-direction: column;
    }

    #nav-title {
        padding: 2rem 0 0.75rem;
    }

    #nav-buttons {
        padding: 0.75rem 0 3rem;
        margin: 0 0 0 auto;
    }

    #lists-container {
        margin-bottom: 5rem;
    }

    #footer-container {
        position: relative;
        height: 45px;
    }

    #footer-container p {
        margin: auto 0;
        font-size: 14px;
    }
}
.modal-footer p {
    margin-right: 1rem;
}

.accordion-button p {
    font-size: 18px;
    margin: auto 0;
}
#display-container h2 {
    font-size: 18px;
    margin: auto 0;
    margin-left: 0.5rem;
}

#display-container p {
    font-size: 18px;
    margin: auto 0.5rem;
}

.accordion-button {
    font-size: 18px;
}

.display-icon {
    width: 28px;
    height: 28px;
    cursor: pointer;
}

.display-icon:hover {
    transform: scale(1.15);
    transition: ease-in-out 0.2s;
}

@media (max-width: 575px) {
    .accordion-button {
        padding: 0.75rem 0.5rem;
        font-size: 16px;
    }

    .accordion-body {
        padding: 0.5rem 0.25rem;
    }

    #display-container {
        flex-direction: column;
    }

    #display-balance {
        justify-content: center;
        margin-bottom: 0.5rem;
    }

    #display-balance p {
        font-size: 16px;
    }

    #display-buttons {
        justify-content: space-between;
    }

    #display-buttons p {
        font-size: 16px;
    }
}
#table-title {
    font-size: 18px;
    margin: 1.5rem 0 1rem;
    text-align: center;
    text-decoration: underline;
}
  
#table-content {
    border-collapse: collapse;
    border: 2px solid black;
}

#table-content th {
    background-color: lightgrey;
    padding: 0.5rem;
    border: 1px solid black;
    font-size: 15px;
    font-weight: 600;
    vertical-align: middle;
    text-align: center;
}
  
#table-content td {
    padding: 0.5rem;
    border: 1px solid black;
    vertical-align: middle;
    text-align: center;
    font-size: 15px;
}

#table-content td input {
    margin: 0;
}

.income {
    background-color: lightgreen;
}

.expense {
    background-color: salmon;
}

@media (max-width: 575px) {
    #statement-wrapper {
        overflow: auto;
    }
    
    #table-title {
        margin: 1.25rem 0 1rem;
        font-size: 16px;
    }

    #table-content {
        width: 540px;
    }
}
#stats-title {
    font-size: 18px;
    margin: 1.5rem 0 1rem;
    text-align: center;
    text-decoration: underline;
}

#statistics-wrapper p {
    margin: 0.25rem 0;
}

#chart-box p {
    display: list-item;
    list-style-type: disc;
    list-style-position: inside;
}

@media (max-width: 768px) {
    #chart-box {
        flex-direction: column;
    }
}

@media (max-width: 575px) {
    #stats-title {
        font-size: 16px;
        margin: 1.25rem 0 1rem;
    }

    #statistics-wrapper p {
        font-size: 15px;
    }

    #chart-box p {
        font-size: 16px;
        text-align: start;
        margin: 1.25rem 0 1rem 0.5rem;
    }
}
.form-input {
    margin-bottom: 0.75rem;
}

#value-input {
    width: 50%;
    margin-right: 0.5rem;
}

#date-input {
    width: 50%;
}

